      <el-form-item label="" prop="contact_person_address">
        <el-input v-model="dataForm.contact_person_address" placeholder="Address" />
      </el-form-item>

      <h2 class="labelSizeInForm">Organization</h2>
      <el-divider />
      <table class="table table-hover table-striped table-dark w-100">
        <thead>
          <tr class="table-active">
            <th>Name of organization</th>
            <th>Member since</th>
            <th>Designation</th>
            <th>Delete</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(item, index) in dataForm.organizationTableData" :key="item">
            <td><el-input v-model="item.org_name" type="text" class="form-control groupName" value="item.groupName" /></td>
            <td><el-input v-model="item.member_since" type="text" class="form-control" value="item.equipmentName" /></td>
            <td><el-input v-model="item.designation" type="text" class="form-control" value="item.description" /></td>
            <td><el-button class="btn btn-danger" @click="deleteGroup(index)"> X </el-button> </td>
          </tr>
        </tbody>
      </table>
      <el-button class="btn btn-info float-right" @click="addGroup">Add</el-button>
      <h2 class="labelSizeInForm">Certificate</h2>
      <el-divider />
      <div style="width: 100%">
        <el-checkbox v-model="certify" />
        I have personally reviewed the information on this application and I certify under penalty of perjury that to the best of my knowledge and behalf the information on this application is true and correct, and that I understand this information is subject to public disclosure
      </div>
    </el-form>
    <span slot="footer" class="dialog-footer">
      <el-button class="cancel" size="medium" @click="exitForm">CANCEL</el-button>
      <el-button size="medium" @click="onSubmit">SAVE</el-button>
    </span>
  </el-dialog>

</template>

<script>
export default {
  name: 'CallOutForm',
  props: [
    'formVisible',
    'formTypes',
    'formType',
    'formData'
  ],
  data() {
    return {
      loading: false,
      titleForm: 'Application For Municipal FisherFolks Registration',
      textMap: {
        add: '',
        edit: ''
      },
      dataForm: {
        id: undefined,
        user_title: '',
        last_name: '',
        first_name: '',
        middle_name: '',
        suffix: '',
        address: '',
        contact_number: '',
        residence_year: '',
        age: '',
        dob: '',
        pob: '',
        gender: '',
        civil_status: '',
        no_children: '',
        nationality: '',
        educational_background_id: '',
        contact_person: '',
        contact_person_relationship: '',
        contact_person_contact_number: '',
        contact_person_address: '',
        organizationTableData: []
      },
      certify: false,
      rules: {}
    }
  },
  watch: {
    formVisible: function() {
      if (this.formTypes) {
        if (this.formType === this.formTypes.add) {
          this.handleCreate()
        } else if (this.formType === this.formTypes.edit) {
          this.handleUpdate(this.formData)
        }
      }
    }
  },
  mounted() {
    this.textMap.add = `Add New ${this.titleForm}`
    this.textMap.edit = `Edit ${this.titleForm}`
  },
  methods: {
    handleCreate() {
      console.log('handleCreate')
      this.loading = false
    },
    handleUpdate(row) {
      console.log('handleUpdate', row)
      this.loading = false
    },
    onSubmit() {
      if (this.formTypes) {
        if (this.formType === this.formTypes.add) {
          this.create()
        } else if (this.formType === this.formTypes.edit) {
          this.update()
        }
      }
    },
    create() {
      this.$refs.dataForm.validate(valid => {
        if (valid) {
          this.loading = true
          this.loading = false
        }
      })
    },
    addGroup() {
      this.dataForm.organizationTableData.push({
        org_name: '',
        member_since: '',
        designation: ''
      })
    },
    deleteGroup(index) {
      this.dataForm.organizationTableData.splice(index, 1)
    },
    update() {},
    resetModel() {
      this.dataForm = {
        id: undefined,
        registrationType: ''
      }
      this.formVisible = false
    },
    exitForm() {
      this.resetModel()
      this.$emit('formVisible')
    },
    successForm() {
      this.$emit('formSuccess')
    }
  }
}
</script>

<style lang="scss" scoped>
.cancel {
  background-color: $cancel;
  color: gray;
}
</style>
