<template>
  <div class="dashboard-container">
    <el-card class="box-card">
      <el-form
        ref="profileValidateForm"
        :model="userProfile"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item
          label="Profile"
        >
          <div v-if="userProfile.imageUrl.length > 0" class="image-preview">
            <img class="preview" :src="userProfile.imageUrl">
          </div>
          <div v-else class="image-preview">
            <img class="preview" src="https://cube.elemecdn.com/3/7c/3ea6beec64369c2642b92c6726f1epng.png">
          </div>
          <div class="file-upload-form">
            <label class="custom-file-upload">
              <input type="file" accept="image/*" @change="previewImage">
              Upload Profile
            </label>
          </div>
        </el-form-item>

        <el-form-item
          v-if="currentUser.registration_type !== 'Student'"
          label="Office"
          prop="office"
        >
          <el-input v-model="userProfile.office" class="formField" />
        </el-form-item>

        <el-form-item
          label="Full Name"
          prop="name"
        >
          <el-input v-model="userProfile.name" class="formField" />
        </el-form-item>

        <el-form-item
          v-if="currentUser.registration_type === 'Student'"
          label="Year"
          prop="year"
        >
          <el-input v-model="userProfile.year" class="formField" />
        </el-form-item>

        <el-form-item
          v-if="currentUser.registration_type === 'Student'"
          label="Section"
          prop="section"
        >
          <el-input v-model="userProfile.section" class="formField" />
        </el-form-item>

        <el-form-item
          label="Email"
          prop="email"
        >
          <el-input v-model="userProfile.email" class="formField" />
        </el-form-item>

        <el-form-item
          label="Password"
          prop="password"
        >
          <el-input v-model="userProfile.password" class="formField" type="password" />
        </el-form-item>

        <el-form-item
          v-if="currentUser.registration_type !== 'Student'"
          label="Referral Code"
          prop="referral"
        >
          <el-input v-model="userProfile.referral" class="formField" />
        </el-form-item>

        <el-form-item
          label="Contact No"
          prop="contact"
        >
          <el-input v-model="userProfile.contact" class="formField" />
        </el-form-item>

        <el-form-item
          label="Address"
          prop="address"
        >
          <el-input v-model="userProfile.address" class="formField" />
        </el-form-item>

        <el-form-item>
          <el-button class="button-primary">Save</el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
export default {
  name: 'Profile',
  data() {
    return {
      currentUser: JSON.parse(localStorage.getItem('user')),
      userProfile: {
        referral: '',
        imageUrl: '',
        office: '',
        name: '',
        year: '',
        section: '',
        email: '',
        password: '',
        contact: '',
        address: ''
      }
    }
  },
  methods: {
    previewImage(event) {
      // Reference to the DOM input element
      var input = event.target
      // Ensure that you have a file before attempting to read it
      if (input.files && input.files[0]) {
        // create a new FileReader to read this image and convert to base64 format
        var reader = new FileReader()
        // Define a callback function to run, when FileReader finishes its job
        reader.onload = (e) => {
          // Note: arrow function used here, so that "this.imageData" refers to the imageData of Vue component
          // Read image as base64 and set to imageData
          this.userProfile.imageUrl = e.target.result
        }
        // Start the reader job - read file as a data url (base64 format)
        reader.readAsDataURL(input.files[0])
      }
    }
  }
}
</script>

