import Layout from '@/layout'
import Login from '@/views/fishery/Login/'
import Register from '@/views/fishery/Register/'
import Dashboard from '@/views/fishery/dashboard'
import Boats from '@/views/fishery/boats'
import Folks from '@/views/fishery/folks'
export const router = [
  {
    path: '/login',
    component: Login,
    hidden: true
  },
  {
    path: '/register',
    component: Register,
    hidden: true
  },
  {
    path: '/',
    component: Layout,
    redirect: '/dashboard',
    children: [
      {
        path: '/dashboard',
        component: Dashboard,
        name: 'Dashboard',
        meta: {
          title: 'Dashboard',
          roles: ['ADMIN']
        }
      }
    ]
  },
  {
    path: '/dashboard',
    component: Layout,
    name: 'Dashboard3',
    meta: {
      title: 'Registration',
      roles: ['ADMIN']
    },
    children: [
      {
        path: '/fisher-folks',
        name: 'Fisherman',
        component: Folks,
        meta: { title: 'Municipal Fisher Folks' }
      },
      {
        path: '/boat',
        name: 'Registered Boat',
        component: Boats,
        meta: { title: 'Boats' }
      }
    ]
  },
  {
    path: '/report',
    component: Layout,
    redirect: '/report',
    children: [
      {
        path: '/report',
        name: 'report',
        meta: {
          title: 'Report',
          roles: ['ADMIN']
        }
      }
    ]
  },
]
